body {
  margin: 0;  
  padding: 0; 
  overflow: hidden; 
}

.box {
  position: relative; 
  width: 100%;       
  height: 100vh;   
}

.box video {
  position: fixed;  
  top: 50%;         
  left: 50%;      
  min-width: 100%;    
  min-height: 100%;    
  width: auto;        
  height: auto;        
  transform: translate(-50%, -50%); 
}
