body {
  margin: 0;  
  padding: 0; 
  overflow: hidden;
}

.title-box {
  padding: 0;
  margin: 0;
  position: fixed; 
  top: 0; 
  left: 0; 
  width: 100%;
  height: 100vh; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
  overflow: hidden; 
}

.title-box::before {
  content: ''; 
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('./titlefront.jpg'); 
  background-size: cover; 
  background-position: center; 
  z-index: 0; 
}

.title-box h1 {
  color: white; 
  font-size: 7rem; 
  z-index: 1;
  margin: 0; 
  text-shadow: 
    1px 1px 0 rgb(255, 149, 0), 
    -1px -1px 0 rgba(0, 0, 0, 0.9), 
    1px -1px 0 rgba(0, 0, 0, 0.9), 
    -1px 1px 0 rgba(0, 0, 0, 0.9); 
}

.title-box p { 
  color: white; 
  font-size: 1.9rem;
  z-index: 1; 
  margin-top: 1rem; 
  text-shadow: 
    1px 1px 0 rgba(0, 0, 0, 0.9), 
    -1px -1px 0 rgba(0, 0, 0, 0.9), 
    1px -1px 0 rgba(0, 0, 0, 0.9), 
    -1px 1px 0 rgba(0, 0, 0, 0.9); 
}

